export default {
	data() {
		return {
			hasAddress: false,
			name: '',
			phoneNum: '',
			address: '请填写收货地址',
			detailAddress: '',
			address_id: '',
			checkPhoneNum: false
		};
	},
	onLoad() {
		let addressInfo = uni.getStorageSync('address')
		this.name = addressInfo.name
		this.phoneNum = addressInfo.phoneNum
		this.address = addressInfo.address
		this.detailAddress = addressInfo.detailAddress
		this.address_id = addressInfo.id
		if (addressInfo.address) {
			this.hasAddress = true
		} else {
			this.hasAddress = false
		}
	},
	methods: {
		getName(e) {
			console.log(e.detail.value)
			if (e.detail.value) {
				this.name = e.detail.value
			}
		},
		getPhoneNum(e) {
			if (e.detail.value) {
				this.phoneNum = e.detail.value
			}
		},
		// 校验手机号格式是否正确
		checkPhone() {
			let reg = /^1[3|4|5|7|8][0-9]{9}$/;
			if (reg.test(this.phoneNum)) {
				this.checkPhoneNum = true
			} else {
				this.checkPhoneNum = false
				uni.showToast({
					title: '请输入正确的手机号',
					duration: 1000,
					icon: 'none'
				})
			}
		},
		getDetailAddress(e) {
			if (e.detail.value) {
				this.detailAddress = e.detail.value
			}
		},
		getAddress() {
			uni.chooseLocation({
				success: (res) => {
					console.log(res)
					this.address = res.address + res.name
					this.hasAddress = true
				}
			})
		},
		// 保存
		keep() {
			if (this.name !== '' && this.phoneNum !== '' && this.address !== '请填写收货地址' && this.detailAddress !== '') {
				this.checkPhone()
				if (this.checkPhoneNum) {
					console.log('保存成功')
					this.$api.addressEdit({
						address_id: this.address_id,
						name: this.name,
						phone: this.phoneNum,
						address: this.address,
						house: this.detailAddress,
						is_confirm: 2
					}).then(res => {
						console.log(res)
						if (res.data.errno === 0) {
							uni.showToast({
								title: '保存成功',
								duration: 1000,
								icon: 'none'
							})
							// 返回上一个页面
							uni.navigateBack({
								delta: 1
							});
						} else {
							uni.showToast({
								title: '保存失败，请重试',
								duration: 1000,
								icon: 'none'
							})
						}
					})
				} else {
					uni.showToast({
						title: '请输入正确的手机号',
						duration: 1000,
						icon: 'none'
					})
				}
			} else {
				uni.showToast({
					title: '资料未填写完整',
					duration: 1000,
					icon: 'none'
				})
			}
		},
		// 删除收货地址
		delAddress() {
			this.$api.addressDel({
				address_id: this.address_id
			}).then(res => {
				console.log(res)
				if (res.data.errno === 0) {
					uni.showToast({
						title: '删除成功',
						duration: 1000,
						icon: 'none'
					})
					uni.navigateBack({
						delta: 1
					})
				} else {
					uni.showToast({
						title: '删除失败，请重试',
						duration: 1000,
						icon: 'none'
					})
				}
			})
		}
	}
}
